import divider from '@/components/divider.vue';

//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';

export default {
	components: {
		divider
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			params: {
				year: true,
				month: true,
				day: true,
				hour: true,
				minute: true,
				second: true,
				province: true,
				city: true,
				area: true,
				timestamp: false, // 1.3.7版本提供
			},
			index: -1,
			musicName:"无",
			startTime: "选择开始时间",
			endTime: "选择结束时间",
			showTime: false,
			open: false,
			open2: false,
			open3: false,
			open4:false,
			activityDetail:"",
			activityRule:"",
			imageList:[],
			imageUrls:"",
			isquane:false,
			isshoufei:false,
			uploadData:{
				   
				    bgm: "", //long | 背景音乐 | 必传    
					open_fee:0 ,//是否收费(0:否;1:是)
					fee_money:"" ,//'佣金',
					uy_money:"",//'购买金额',
					rule:"",
					open_area_limit:"",
				   
				    description: "", //string | 活动描述 | 必传    
				    end_time: "", //datetime | 结束时间 | 必传    
				   
				   
				    good_price: "", //long | 商品原价 | 必传    
				    good_pt_price: "", //long | 商品拼团价 | 必传    
				    good_stock: "", //int | 商品库存 | 必传    
				    head_pic: "", //string | 头图 | 必传    
				   
				    open_advance_pay: 0, //boolean | 是否预付款(0:否;1:是) | 必传    
				    open_auto_pt: 0, //boolean | 是否自动成团(0:否;1:是) | 必传    
				    open_mobile: 0, //boolean | 是否需要填手机号(0:否;1:是) | 必传    
				    pic: "", //string | 图片数据 | 必传    
				    pt_advance_price: "", //long | 平团预付价 | 必传    
				    pt_time: "", //datetime | 成团时间 | 必传    
				    pt_user_count: "", //int | 拼团人数 | 必传    
				    
				    start_time: "", //datetime | 开始时间 | 必传    
				    title: "", //string | 活动标题 | 必传    
				   
			}
		}
	},
	
	
	
	methods: {
		
		// 活动头图
		chooseBg() {
			uni.chooseImage({
				count: 1, //默认9
				success: (res) => {
					this.bgImage = res.tempFilePaths[0]
					this.upLoadImage(this.bgImage,"topImg")
					
				}
			});
		},
		
		
		upLoadImage(data,type){
			
			uni.showLoading({
				title:'上传图片中...',
			})
			
			uni.uploadFile({
				url:$C.baseUrl + '/activity_pt/tob/detail',
				filePath:data,
				name:"file",
				header:{authorization:this.token},
				success: (res) => {
					uni.hideLoading()
					console.log(res)
					
				},
				fail: (err) => {
					uni.hideLoading()
					console.log(err);
					
				}
			})
			
		
		},
		
		
		// 是否需要手机号
		needPhone(e){
			if(e){
				this.uploadData.open_mobile=1
			}
			else{
				this.uploadData.open_mobile=0
			}
		},
		
		// 是否全额
		isQuane(e){
			this.isquane=e
			if(e){
				this.uploadData.open_advance_pay=1
				this.uploadData.pt_advance_price=""
			}
			else{
				this.uploadData.open_advance_pay=0
			}
		},
		
		// 地区放刷
		areaFangShua(e){
			if(e){
				this.uploadData.open_area_limit=1
			}
			else{
				this.uploadData.open_area_limit=0
			}
		},
		
		// 是否收费
		isShouFei(e){
			this.isshoufei=e
			if(e){
				this.uploadData.open_fee=1
			}
			else{
				this.uploadData.open_fee=0
			}
		},
		
		// 选择营业时间
		selectTime(num) {
			this.showTime = true
			if (num == 1) {
				this.index = 1
			} else {
				this.index = 2
			}
		},
		chooseTime(res) {
			console.log(res)
			if (this.index == 1) {
				this.startTime = res.year + "-" + res.month + "-" + res.day + " " + res.hour + ":" + res.minute+":"+res.second;
				this.uploadData.start_time=this.startTime
			} else {
				this.endTime = res.year + "-" + res.month + "-" + res.day + " "  + res.hour + ":" + res.minute+":"+res.second;
				this.uploadData.end_time=this.endTime
			}
		},


		chooseImage(){
			if(this.imageList.length<=9){
				uni.chooseImage({
				   count: 9, //默认9
				   success: (res) => {
				   	console.log(res.tempFilePaths);
					this.imageUrls=res.tempFilePaths
					for (var i = 0; i < res.tempFilePaths.length; i++) {
						let data={
							type:"pic",
							src:res.tempFilePaths[i],
						}
						this.imageList.push(data)
					}
					
				   },
				   fail: (err) => {
				   	console.log(err)
				   }
				});
			}
			else{
				uni.showToast({
					icon:"none",
					title:"最多上传9张"
				})
			}
		
		},
		
		previewImage(url){
			uni.previewImage({
				current:url,
				urls:this.imageUrls,
				indicator:"default",
				success: (res) => {
					
				},
				fail: (err) => {
					console.log(err)
				}
			})
		},
		
		confirmFabu(){
			console.log(this.uploadData)
			// let data=this.uploadData
			// 发送到服务端
			$H.post("/activity_pt",this.uploadData).then((res) => {
				//请求成功
			  console.log(res)
			
			}).catch((e) => {
			
				//请求失败
				console.log("失败" + e)
			})
			
		}
	}
}
